import * as GWU from 'gw-utils';
import { Grid } from './grid';
import { Text } from './text';
export class Term {
    constructor(ui) {
        this.x = 0;
        this.y = 0;
        this.widgets = [];
        this._currentWidget = null;
        this._grid = null;
        this.ui = ui;
        this.reset();
    }
    get buffer() {
        return this.ui.buffer;
    }
    get width() {
        return this.ui.width;
    }
    get height() {
        return this.ui.height;
    }
    // COLOR
    reset() {
        this._style = Object.assign({}, Term.default);
        this._focusStyle = {};
        this._hoverStyle = {};
        return this;
    }
    fg(v) {
        this._style.fg = v;
        return this;
    }
    bg(v) {
        this._style.bg = v;
        return this;
    }
    dim(pct = 25, fg = true, bg = false) {
        if (fg) {
            this._style.fg = GWU.color.from(this._style.fg).darken(pct);
        }
        if (bg) {
            this._style.bg = GWU.color.from(this._style.bg).darken(pct);
        }
        return this;
    }
    bright(pct = 25, fg = true, bg = false) {
        if (fg) {
            this._style.fg = GWU.color.from(this._style.fg).lighten(pct);
        }
        if (bg) {
            this._style.bg = GWU.color.from(this._style.bg).lighten(pct);
        }
        return this;
    }
    invert() {
        [this._style.fg, this._style.bg] = [this._style.bg, this._style.fg];
        return this;
    }
    style(opts) {
        this._style = Object.assign({}, Term.default, opts);
        return this;
    }
    focusStyle(opts) {
        this._focusStyle = opts;
        return this;
    }
    hoverStyle(opts) {
        this._hoverStyle = opts;
        return this;
    }
    // POSITION
    pos(x, y) {
        this.x = GWU.clamp(x, 0, this.width);
        this.y = GWU.clamp(y, 0, this.height);
        return this;
    }
    moveTo(x, y) {
        return this.pos(x, y);
    }
    move(dx, dy) {
        this.x = GWU.clamp(this.x + dx, 0, this.width);
        this.y = GWU.clamp(this.y + dy, 0, this.height);
        return this;
    }
    up(n = 1) {
        return this.move(0, -n);
    }
    down(n = 1) {
        return this.move(0, n);
    }
    left(n = 1) {
        return this.move(-n, 0);
    }
    right(n = 1) {
        return this.move(n, 0);
    }
    nextLine(n = 1) {
        return this.pos(0, this.y + n);
    }
    prevLine(n = 1) {
        return this.pos(0, this.y - n);
    }
    // EDIT
    // erase and move back to top left
    clear(color) {
        return this.erase(color).pos(0, 0);
    }
    // just erase screen
    erase(color) {
        // remove all widgets
        if (color === undefined) {
            color = this._style.bg;
        }
        this.buffer.fill(' ', color, color);
        return this;
    }
    eraseBelow() {
        // TODO - remove widgets below
        this.buffer.fillRect(0, this.y + 1, this.width, this.height - this.y - 1, ' ', this._style.bg, this._style.bg);
        return this;
    }
    eraseAbove() {
        // TODO - remove widgets above
        this.buffer.fillRect(0, 0, this.width, this.y - 1, ' ', this._style.bg, this._style.bg);
        return this;
    }
    eraseLine(n) {
        if (n === undefined) {
            n = this.y;
        }
        if (n >= 0 && n < this.height) {
            // TODO - remove widgets on line
            this.buffer.fillRect(0, n, this.width, 1, ' ', this._style.bg, this._style.bg);
        }
        return this;
    }
    eraseLineAbove() {
        return this.eraseLine(this.y - 1);
    }
    eraseLineBelow() {
        return this.eraseLine(this.y + 1);
    }
    // GRID
    // erases/clears current grid information
    grid() {
        this._grid = new Grid(this.x, this.y);
        return this;
    }
    cols(...args) {
        if (!this._grid)
            return this;
        this._grid.cols(args[0], args[1]);
        return this;
    }
    rows(...args) {
        if (!this._grid)
            return this;
        this._grid.rows(args[0], args[1]);
        return this;
    }
    startRow(n) {
        if (!this._grid)
            return this;
        if (n !== undefined) {
            this._grid.row(n);
        }
        else {
            this._grid.nextRow();
        }
        this.pos(this._grid.x, this._grid.y);
        return this;
    }
    nextCol() {
        if (!this._grid)
            return this;
        this._grid.nextCol();
        this.pos(this._grid.x, this._grid.y);
        return this;
    }
    endRow() {
        return this;
    }
    // moves to specific column
    col(n) {
        if (!this._grid)
            return this;
        this._grid.col(n);
        this.pos(this._grid.x, this._grid.y);
        return this;
    }
    // moves to specific row
    row(n) {
        if (!this._grid)
            return this;
        this._grid.row(n);
        this.pos(this._grid.x, this._grid.y);
        return this;
    }
    // DRAW
    drawText(text, width, _align) {
        const widget = new Text(this.x, this.y, text, { width });
        widget.normalStyle(this._style);
        widget.draw(this.buffer);
        return this;
    }
    border(w, h, bg) {
        const c = bg || this._style.fg;
        const buf = this.buffer;
        GWU.xy.forBorder(this.x, this.y, w, h, (x, y) => {
            buf.draw(x, y, ' ', c, c);
        });
        return this;
    }
    // WIDGETS
    get() {
        return this._currentWidget;
    }
    widgetAt(...args) {
        return this.widgets.find((w) => w.contains(args[0], args[1])) || null;
    }
    text(text, width, _align) {
        const widget = new Text(this.x, this.y, text, { width });
        widget.normalStyle(this._style);
        widget.hoverStyle(this._hoverStyle);
        widget.focusStyle(this._focusStyle);
        widget.draw(this.buffer);
        this._currentWidget = widget;
        this.widgets.push(widget);
        return this;
    }
    // CONTROL
    render() {
        this.ui.render();
        return this;
    }
    // EVENTS
    mousemove(e) {
        const w = this.widgetAt(e);
        this.widgets.forEach((w2) => {
            w2.hovered = w2 === w;
        });
        return false;
    }
    draw() {
        this.widgets.forEach((w) => w.draw(this.buffer));
        this.render();
    }
}
Term.default = {
    fg: 'white',
    bg: 'black',
    align: 'left',
    valign: 'top',
};
