import * as GWU from 'gw-utils';
export class Widget {
    constructor(x, y, opts = {}) {
        this.bounds = new GWU.xy.Bounds(-1, -1, 0, 1);
        this._normalStyle = {};
        this._hoverStyle = {};
        this._focusStyle = {};
        this._focus = false;
        this._hover = false;
        this.activeStyle = {
            fg: 'white',
            bg: -1,
            align: 'left',
            valign: 'top',
        };
        this.bounds.x = x;
        this.bounds.y = y;
        if (opts.style)
            this._normalStyle = opts.style;
        if (opts.focus)
            this._focusStyle = opts.focus;
        if (opts.hover)
            this._hoverStyle = opts.hover;
        this._updateStyle();
    }
    contains(...args) {
        return this.bounds.contains(args[0], args[1]);
    }
    normalStyle(opts) {
        this._normalStyle = opts;
        this._updateStyle();
    }
    hoverStyle(opts) {
        this._hoverStyle = opts;
        this._updateStyle();
    }
    focusStyle(opts) {
        this._focusStyle = opts;
        this._updateStyle();
    }
    get focused() {
        return this._focus;
    }
    set focused(v) {
        this._focus = v;
        this._updateStyle();
    }
    get hovered() {
        return this._hover;
    }
    set hovered(v) {
        this._hover = v;
        this._updateStyle();
    }
    _updateStyle() {
        this.activeStyle = Object.assign({}, Widget.default, this._normalStyle);
        if (this._focus) {
            Object.assign(this.activeStyle, this._focusStyle);
        }
        else if (this._hover) {
            Object.assign(this.activeStyle, this._hoverStyle);
        }
    }
}
Widget.default = {
    fg: 'white',
    bg: -1,
    align: 'left',
    valign: 'top',
};
